.counter-container {
    .counter-intro {
        h1 {
            font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h1, 2));
            font-size: clamp(2rem, 4vw, calc(var(--font-size__base, 18px) * var(--font-factor__h1, 2)));
        }
    }

    .counter {
        .counter--content .content--title {
            font-size: 1.5rem;
        }

        &.counter__with-icon {
            .counter--data,
            .counter--type {
                font-size: 3rem;
                font-size: clamp(2.25rem, 4vw, 3rem);
            }
            .counter--icon {
                font-size: 3.75rem;
                font-size: clamp(2.5rem, 4vw, 3.75rem);
            }
        }
    }
}