// Contact (Open Content)
.contact {
    .contact--phone {
        position: relative;

        &::before {
            content: "\f095";
            font-family: var(--font__awesome);
            font-weight: 600;
            margin-right: 0.5em;
        }
    }
    .contact--email {
        position: relative;

        &::before {
            content: "\f0e0";
            font-family: var(--font__awesome);
            font-weight: 600;
            margin-right: 0.5em;
        }
    }
}