// Accordion  (Open Content)
.iif {
    .accordion-container {
        .accordion-tab {
            border-color: #d5d5d5;
        }
        .accordion-content {
            .accordion-content--wrapper {
                border-color: #d5d5d5;
                border-left: 0;
                border-right: 0;
                border-bottom: 0;
            }
        }
    }
}