// Global Nav
.global-nav {
    order: 2;
    flex: 1 1 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
    padding: 1em 0;
}

// Main Nav
.header--nav {
    width: 100%;

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        width: 100%;

        &.menu__main {
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-end;
        }

        li {
            flex: 1 1 100%;

            a.menu-link,
            span.menu-link {
                position: relative;
                display: block;
                font-size: 1.25rem;
                font-weight: 900;
                color: var(--brand-blue);
                padding: 1em;
                border-bottom: 1px solid #ddd;
                transition: all var(--timing, 0.4s) var(--timing__function, ease);
                text-transform: uppercase;

                .submenu-toggle {
                    display: none;
                }

                &.menu-link__parent {
                    .submenu-toggle {
                        display: block;
                        position: absolute;
                        top: 0;
                        right: 0;
                        height: 100%;
                        width: 50px;
    
                        * {
                            pointer-events: none;
                        }
    
                        .icon {
                            width: 1em;
                            height: 1em;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            fill: var(--theme-nav-link, #00E);
                            pointer-events: none;
                        }
                    }
                }

                &.menu-link__back {
                    text-transform: uppercase;
                    padding: 1em 1em 1em 6em;
                    font-size: 0.85em;

                    .back-toggle {
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 100%;
                        width: 5em;
                        border-right: 1px solid #ddd;

                        .icon {
                            width: 1em;
                            height: 1em;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            fill: var(--theme-nav-link, #00E);
                            pointer-events: none;
                        }
                    }
                }
            }

            ul.menu__submenu {
                position: absolute;
                top: 0;
                left: 100%;
                background-color: var(--theme-nav-bg, hsla(198, 80%, 62%, 100%));
                width: 100%;
                transition: transform 0s;
                z-index: 1;
                transform-origin: left;

                &[aria-hidden='true'] {
                    transform: scaleX(0) translate(0);
                    transition: transform var(--timing, 0.4s) ease;
                }

                li {
                    a.menu-link,
                    span.menu-link {
                        text-transform: none;
                    }
                }
            }
        }
    }
}

.iif.iif__home {
    .global-nav {
        top: clamp(88px, 45vh, 155px); // smallest size, scaler, & largest size;
        height: calc(100% - 150px);
        position: relative;
        align-items: flex-start;
    }
    .header--nav {
        top: 0;
        text-align: right;
        padding-right: 3rem;

        ul {
            li {
                flex: 1 1 100%;

                a.menu-link, 
                span.menu-link {
                    width: auto;
                    display: inline-block;
                    color: var(--theme-white, #fff);
                    border: 0;
                    margin: 1rem;
                    padding: 0;
                    font-size: 1.5rem;
                    
                    span {
                        transition: all var(--timing__fast) var(--timing__function);
                        border-bottom: 5px solid transparent;
                    }

                    &.menu-link__parent {
                        .submenu-toggle {
                            display: none !important;
                        }
                    }

                    &:hover {
                        span {
                            border-bottom-color: var(--brand-accent, #000);
                        }
                    }
                }
            }
        }
    }
}


@media screen and (min-width: @breakpoint__desktop) {
    .global-nav {
        padding: 0;
        order: 3;
        margin: 3rem 0 2rem;
    }


    .header--nav {
        ul {
            li {
                position: relative;
                flex: 0 1 auto;

                a.menu-link,
                span.menu-link {
                    font-size: 1.875rem;
                    font-size: clamp(1.25rem, 1.5vw, 1.875rem); // smallest size, scaler, & largest size
                    display: block;
                    width: 100%;
                    border: 0;
                    padding: 0.5em 2rem;
                    border-bottom: 5px solid transparent;
                    transition: color var(--timing) var(--timing__function), background-color var(--timing) var(--timing__function), border var(--timing__fast) var(--timing__function);

                    &:hover {
                        color: lighten(hsla(217, 91%, 23%, 100%), 15%);
                        border-bottom: 5px solid var(--brand-blue, #000);
                    }

                    &.menu-link__breadcrumb {
                        border-bottom: 5px solid var(--brand-accent, #000);
                    }

                    .submenu-toggle {
                        display: none;
                    }

                    &.menu-link__parent {
                        .submenu-toggle {
                            display: none;
                        }
                    }

                    &.menu-link__back {
                        display: none;
                    }
                }

                ul.menu__submenu {
                    position: absolute;
                    top: calc(100% - 5px); // to compensate for borders
                    left: 0;
                    transform: scaleY(0) scaleX(1);
                    transform-origin: top;
                    opacity: 0;
                    min-width: 15em;
                    padding: 0.75em 0;
                    transition: all var(--timing__fast, 0.25s) var(--timing__function, ease);
                    transform-origin: top;
                    border-top: 5px solid var(--brand-blue, #000);

                    a.menu-link,
                    span.menu-link {
                        font-size: 1.175rem;
                        padding: 0.5em 2rem;
                        font-weight: 600;
                        border: 0;
                    }

                    li {
                        &:hover,
                        &.menu-item__breadcrumb {
                            background-color: darken(hsl(200deg, 27%, 87%), 5%);
                        }
                    }

                    &[aria-hidden='true'] {
                        transform: scaleY(0) scaleX(1);
                        transition: all var(--timing__fast, 0.25s) var(--timing__function, ease);
                    }

                    &[data-visible='false'] {
                        left: auto;
                        right: 0;

                        ul {
                            left: auto !important;
                            right: 100% !important;
                            transform-origin: right;
                            transform: scaleY(1) scaleX(0);
                        }
                    }

                    &[data-level='1'] {
                        top: 0;
                        left: 100%;
                        transform: scaleY(1) scaleX(0);
                        transform-origin: left;
                        background-color: var(--theme-subnav-bg);

                        &[data-visible='false'] {
                            left: auto !important;
                            right: 100% !important;
                            transform-origin: right;
                            transform: scaleY(1) scaleX(0);

                            ul.menu__submenu {
                                left: auto !important;
                                right: 100%;
                            }
                        }
                    }
                    &[data-level='2'] {
                        left: 100%;
                        top: 0;
                        background-color: var(--theme-subnav-bg-lvl2);
                        transform-origin: left;
                        transform: scaleY(1) scaleX(0);

                        &[data-visible='false'] {
                            left: auto !important;
                            right: 100% !important;
                            transform-origin: right;
                            transform: scaleY(1) scaleX(0);

                            ul.menu__submenu {
                                left: auto !important;
                                right: 100%;
                            }
                        }
                    }
                }

                &:hover,
                &:active,
                &:focus,
                &:focus-within {
                    &[data-level='1'] {
                        > a.menu-link {
                            //background-color: var(--theme-link-bg-hover, hsla(198, 80%, 62%, 100%));
                        }
                    }
                    &[data-level='2'] {
                        > a.menu-link {
                            //background-color: var(--theme-subnav-bg);
                        }
                    }
                    &[data-level='3'] {
                        > a.menu-link {
                            //background-color: var(--theme-subnav-bg-lvl2);
                        }
                    }
                    &[data-level='4'] {
                        > a.menu-link {
                            //background-color: var(--theme-subnav-bg-lvl3);
                            //color: #fff;
                        }
                    }

                    > ul.menu__submenu {
                        transform: scaleY(1);
                        opacity: 1;
                        z-index: 2;
                    
                        &[data-level='1'] {
                            left: 100%;
                            transform: scaleY(1) scaleX(1);
                        }
                        &[data-level='2'] {
                            left: 100%;
                            transform: scaleY(1) scaleX(1);
                        }
                    }
                }
            }
        }
    }

    .iif.iif__home {
        .global-nav {
            top: calc(156px + 1.5rem);
            height: calc(100% - (156px + 1.5rem));
            position: relative;
            align-items: flex-start;
            .etg-row-container(calc(var(--site-width, 2000px) + 8rem));
            padding: 3rem 2rem 0;
        }

        .header--nav {
            top: 0;
            text-align: right;
            padding-right: 2rem;
    
            ul {
                li {
                    a.menu-link, 
                    span.menu-link {
                        margin: 1rem 0;
                        font-size: 1.875rem;
                    }

                    ul.menu__submenu {
                        display: none !important;
                    }
                }
            }
        }
    }
}