// Utility Navigation (Site Header)
.utility-nav {
    order: 3;
    flex: 1 1 100%;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    color: var(--theme-link);
    text-align: center;
    .etg-row-container(calc(var(--site-width, 2000px) + 8rem));

    a {
        flex: 1 1 150px;
        display: block;
        font-size: 1rem;
        font-weight: 300;
        color: var(--brand-blue);
        transition: color var(--timing__fast, 0.25s) var(--timing__function, ease);
        text-transform: uppercase;
        padding: 1em;
 
        &:hover,
        &:focus,
        &:focus-within,
        &:active {
            color: var(--brand-accent, #00E);
        }

        &.button {
            border-width: 2px;
            padding: 0.275em 1em;
        }
    }

    .utility-nav--user {
        overflow: hidden;
        width: 1.25em;
        height: 1.25em;
        color: transparent;
        position: relative;
        font-size: 1.25em;
        color: transparent;

        &::before {
            content: '\f007';
            font-family: var(--font__awesome);
            line-height: 1;
            color: var(--theme-link);
            font-size: 1em;
            font-weight: 600;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: color var(--timing__fast, 0.25s) var(--timing__function, ease);
        }

        &:hover,
        &:active,
        &:focus,
        &:focus-within {
            color: transparent;
            &::before {
                color: var(--brand-accent, #00E);
            }
        }
    }
}

@media screen and (min-width: @breakpoint__desktop) {
    .utility-nav {
        order: 2;
        flex: 0 0 auto;
        padding: 0 1em;

        a {
            padding: 0 0 0 0.75em;
            margin: 0 0 0 0.75em;
            flex: 0 0 auto;
            border-left: 1px solid var(--brand-accent, #000);

            &:first-child {
                border: 0;
            }
        }
    }
}