/// Variables ///
:root {

	// Colors

	--brand-blue: hsla(217, 91%, 23%, 100%);
	--brand-gold: hsla(44, 93%, 47%, 100%);
	--brand-grey: hsla(0, 0%, 35%, 100%);
	--brand-orange: hsla(27, 63%, 51%,100%);
	--brand-magenta: hsla(330, 100%, 30%, 100%);
	--brand-purple: hsla(313, 40%, 33%, 100%);
	--brand-bright-blue: hsla(200, 100%, 36%, 100%);
	--brand-green: hsla(171, 32%, 28%, 100%);

	--brand-primary: var(--brand-blue);
	--brand-accent: var(--brand-gold);	

	--theme-text: hsla(0, 0%, 22%, 100%);
	--theme-heading: var(--theme-text);
	--theme-border: hsla(0, 0%, 82%, 100%);
	--theme-callout: hsl(0, 0%, 95%);
	--theme-link: var(--brand-blue);
	--theme-link-hover: var(--brand-bright-blue);
	--theme-white: hsla(0, 0%, 98%, 100%);
	--theme-neutral: hsl(0, 0%, 96%);
	--theme-black: hsla(0, 0%, 7%, 100%);
	
	// needs work still
	--theme-banner-shadow: linear-gradient(35deg, hsla(210, 36%, 30%, 0.7), transparent);
	--theme-gradient: linear-gradient(var(--brand-blue), hsl(195, 29%, 97%));

	--theme-nav-bg: hsl(0, 0%, 89.8%);
	--theme-subnav-bg: hsla(207, 52%, 84%, 100%);
	--theme-subnav-bg-lvl2: hsl(206, 64%, 74%);
	--theme-subnav-bg-lvl3: hsl(206, 54%, 59%);
	--theme-nav-link: var(--theme-link);
	--theme-nav-link-hover: var(--theme-link);
	--theme-link-bg: transparent;
	--theme-link-bg-hover: var(--theme-nav-bg);
	--theme-brand-nav-accent: var(--theme-nav-subnav);

	--theme-footerbg: hsla(215, 100%, 6%, 100%);


	// Fonts & Typography
	--font__sans-serif: 'Avenir', Helvetica, Arial, sans-serif;
	--font__serif: 'Martel', serif;
	--font__awesome: "Font Awesome 5 Pro";
	--font-size__base: 16px;
	--line-height__base: 1.5;
	
	--font-factor__h1: 3.25;
	--font-factor__h2: 2.5;
	--font-factor__h3: 2;
	--font-factor__h4: 1.5;
	--font-factor__h5: 1.25;
	--font-factor__h6: 1.2;
	--font-factor__smaller: 10%;

	// Animations
	--timing: 0.4s;
	--timing__fast: 0.25s;
	--timing__slow: 0.7s;
	--timing__snail: 1s;
	--timing__function: ease;
	--timing__ease-in-out: ease-in-out;

	// Grid
	--grid-gutter-width: 2rem; // Padding between columns
	--min-grid-width: 320px; // Minimum width for content in the grid system
	--max-grid-width: 2000px; // Maximum width for content in the grid system
	--site-width: var(--max-grid-width);
}

/// Breakpoints ///
@breakpoint__small: 500px;
@breakpoint__tablet: 700px;
@breakpoint__large: 900px;
@breakpoint__desktop: 1280px;