a,
a.SkinObject {
    color: var(--theme-link, #00E);
    text-decoration: none;

    &:hover {
        color: var(--theme-link-hover, #551A8B);
    }
    &:focus {
        color: var(--theme-link-hover, #551A8B);
    }
    &:focus-within {
        outline: 3px dotted var(--brand-bright-blue, #00e) !important;
    }
    &:active {
        outline: 0 !important;

        &:focus-within {
            outline: 0 !important;
        }
    }

    &:visited {
        color: var(--theme-link-hover, #551A8B);

        &:hover {
            color: var(--theme-link-hover, #551A8B);
        }
        &:focus {
            color: var(--theme-link-hover, #551A8B);
        }
    }
}

a img {
    border: none;
}

a[href^='mailto:'],
a[target='_blank'] {
    // These links tend to be long and could break layout
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;
    hyphens: auto;
