a.button,
.button,
button.button {
    font-family: var(--font__sans-serif);
    display: inline-block;
    user-select: none;
    text-decoration: none;
    vertical-align: middle;
    text-align: center;
    padding: 0.75em 1.5em;
    font-weight: 600;
    border-radius: 0;
    background: transparent;
    background-image: none;
    transition: all .15s ease-in-out;
    box-shadow: none;
    text-shadow: none;

    &.button__large {
        font-size: 1.125rem;
        padding: 1.25em 2.5em;
    }
    &.button__small {
        font-size: 0.813rem;
        padding: 0.5em 0.75em;
    }
}

a.button.button__primary,
.button.button__primary,
button.button__primary {
    
    color: var(--theme-white, #fff);
    
    font-weight: 700;
    line-height: var(--line-height__base, 1.5);
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 0.2em solid var(--brand-bright-blue, #00e);
    background-color: var(--brand-blue, #00e);

    &:hover,
    &:active,
    &:focus {
        color: var(--theme-white, #fff);
        background-color: var(--brand-bright-blue, #00e);
        border-color: var(--brand-bright-blue, #00e);
    }

    &:focus-within {
        outline: 3px dotted var(--brand-bright-blue, #00e) !important;
    }
}


a.button.button__secondary,
.button.button__secondary,
button.button__secondary {
    color: var(--theme-text, #333);
    line-height: var(--line-height__base, 1.5);
    text-transform: uppercase;
    border: 0.2em solid var(--theme-border, #eee);
    background: var(--theme-border, #eee);
    background-color: var(--theme-border, #eee);
   

    &:hover,
    &:active,
    &:focus {
        color: var(--theme-text, #333);
        background: hsla(0, 0%, 75%, 100%);
        background-color: hsla(0, 0%, 75%, 100%);
        border-color: hsla(0, 0%, 75%, 100%);
    }

    &:focus-within {
        outline: 3px dotted var(--brand-bright-blue, #00e) !important;
    }
}

a.button.button__accent,
.button.button__accent,
button.button__accent {
    color: var(--brand-blue, #00e);
    line-height: var(--line-height__base, 1.5);
    text-transform: uppercase;
    border: 0.2em solid var(--brand-accent, #eee);
    

    &:hover,
    &:active,
    &:focus {
        color: var(--brand-blue, #00E);
        background: var(--brand-accent, #00E);
        border-color: var(--brand-accent, #00E);
    }

    &:focus-within {
        outline: 3px dotted var(--brand-bright-blue, #00e) !important;
    }
}



