// Back to Top //
.back-to-top {
    position: fixed;
    float: right;
    bottom: 1em;
    right: 1em;
    width: 0;
    height: 0;
    z-index: 9999;
    opacity: 0;
    transition: all var(--timing, 0.4s) var(--timing__function, ease);

    .icon {
        width: 50px;
        height: 50px;
        fill: var(--brand-bright-blue, #00E);
        opacity: 0.6;
        transition: all var(--timing, 0.4s) var(--timing__function, ease);
        pointer-events: none;
    }

    &:hover {
        cursor: pointer;
        .icon {
            opacity: 1;
        }
    }

    &.visible {
        opacity: 1;
        height: 50px;
        width: 50px;
    }
}

// Print
@media print {
    a {
        text-decoration: underline !important; // Overrides any deeper styles
    }
}
