.table-responsive {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}

.DnnModule-DNN_HTML table {
    border-collapse: collapse;
    
    caption {
        padding-top: .75rem;
        padding-bottom: .75rem;
        color: var(--theme-border, #eee);
        text-align: left;
        caption-side: bottom;
    }

    tr {}
    td {}
    th {
        text-align: inherit;
        text-align: -webkit-match-parent;
    }

    thead {
        tr {}
        th {}
    }

    tbody {
        tr {}
        th,
        td {}
        th {}
        td {}
    }

    tfoot {
        tr {}
        td {}
    }

    &.table {
        width: 100%;
        margin-bottom: 1rem;
        color: #212529;

        th, td {
            padding: .75rem;
            vertical-align: top;
            border-top: 1px solid var(--theme-border, #eee);
        }

        thead {
            th {
                vertical-align: bottom;
                border-bottom: 2px solid var(--brand-grey, #eee);
                background-color: var(--brand-gold);
            }
        }
    }

    &.table-striped {
        tbody {
            tr {
                &:nth-of-type(odd) {
                    background-color: var(--theme-neutral, rgba(0,0,0,.05));
                }
            }
        }
    }
}