/// Shared List Styles ///
ul,
ol {
    margin: 0 0 0 1.25em;
    padding: 0;

    li {
        margin-bottom: 0.5rem;
    }

    ul,
    ol {
        li {
        }
    }
}

/// Unordered Lists ///

ul {
    list-style: square;

    > li {
    }

    ul {
        li {
        }
    }

    &.accent-list {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
            display: flex;
            align-items: center;
            position: relative;

            &::before {
                content: "\10f069";
                position: relative;
                margin-right: 1em;
                color: var(--brand-accent, hsla(210, 100%, 24%, 100%));
                font-family: "Font Awesome 5 Duotone";
                font-size: 1em;
                font-weight: 900;
                height: 100%;
                width: auto;
                opacity: 0.6;
            }
            &::after {
                content: "\f069";
                position: absolute;
                top: 50%;
                left: 0;
                margin-right: 1em;
                color: var(--brand-accent, hsl(210, 100%, 24%));
                font-family: "Font Awesome 5 Duotone";
                font-size: 1em;
                font-weight: 900;
                width: auto;
                opacity: 1;
                transform: translateY(-50%);
            }
        }
    }

    &.intro-list {
        display: flex;
        flex-wrap: wrap;

        li {
            list-style: none;
            margin: 0 0.75rem 0 0;
            padding: 0 0.75rem 0 0;
            border-right: 2px solid var(--brand-bright-blue, #00E);

            &:last-child {
                margin-right: 0;
                border-right: 0;
            }
        }
    }
}

.module-container--body li {
    margin-bottom: 0.5em;
}

