.iif {
    .iif-cta {
        position: relative;

        &::before {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: #052d6e;
            background: linear-gradient(to bottom right, var(--brand-bright-blue), var(--brand-blue) 45%);
            opacity: 0.5;
            z-index: 2;
        }
        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: #071626;
            opacity: 0.75;
            z-index: 1;
        }

        .iif-cta--content {
            max-width: var(--site-width, 2000px);
            z-index: 2;
            position: relative;
            color: #fff;

            p {
                font-size: 1.25rem;
            }
        }
        
        .iif-cta--label {
            padding: 0.5em 0.25em;
            border: 3px solid var(--brand-accent, #000);
            color: var(--brand-accent, #000);
            text-transform: uppercase;
            font-weight: 900;
            margin: 0 2rem;
        }

        .iif-cta--heading {
            border-bottom: 3px solid var(--brand-bright-blue, #00FE);
            display: inline-block;
            padding-bottom: 0.5rem;
            margin-bottom: 1rem;
            font-size: 1.5rem;
            font-family: var(--font__serif);
        }

        .iif-cta--text {
            flex: 1 1 100%;
            margin: 2rem;
        }

        .iif-cta--action {
            justify-content: center;
            margin: 2rem;

            .button {
                font-weight: 400;
                letter-spacing: 1px;
                padding: 1em 1.75rem;
                font-size: 1.25rem;
            }
        }
    }
}

@media screen and (min-width: @breakpoint__small) {
    .iif {
        .iif-cta {
            .iif-cta--text {
                margin: 2rem 3rem;
            }
            .iif-cta--action {
                margin: 2rem 3rem;
                .button {
                    padding: 1em 2.5em;
                }
            }
        }
    }    
}

@media screen and (min-width: @breakpoint__large) {
    .iif {
        .iif-cta {
            .iif-cta--label {
                writing-mode: tb;
                transform: rotate(-180deg);
                margin: 0 0 0 1rem;
            }
            .iif-cta--text {
                flex: 1 1 600px;
                max-width: 800px;
            }
        }
    }
}

@media screen and (min-width: @breakpoint__desktop) {
    .iif {
        .iif-cta {
            .iif-cta--action {
                justify-content: flex-end;
                margin: 2rem 4rem 2rem 3rem;
            }
        }
    }
}