.iif {
    .quick-links {

    }
    .etg-row.etg-row__intro {
        .intro-pane {
            .quick-links {
                display: flex;
                flex-wrap: wrap;
                color: var(--theme-white, #fff);
                margin: 1rem auto 2rem;
    
                ul {
                    list-style: none;
                    max-width: var(--site-width, 2000px);
                    display: flex;
                    flex-wrap: wrap;
                    transition: all var(--timing) var(--timing__function);
                    justify-content: space-between;
                    width: 100%;
            
                    li {
                        flex: 1 1 auto;
                        display: flex;
                        flex-wrap: wrap;
                        margin: 1rem;
            
                        a {
                            color: var(--theme-white, #fff);
                            font-size: 1.25rem;
                            font-weight: 100;
                            display: inline-block;
                            text-transform: uppercase;
                            position: relative;
    
                            &::before {
                                content: '';
                                position: absolute;
                                bottom: 0;
                                right: 0;
                                width: 0;
                                height: 2px;
                                background-color: var(--theme-white, #fff);
                                transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
                            }
            
                            &:hover {
                                color: var(--theme-white, #fff);
    
                                &::before {
                                    left: 0;
                                    right: auto;
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
    
                button.quick-links--toggle {
                    color: var(--theme-white, #fff);
    
                    svg {
                        fill: var(--theme-white, #fff);
                    }
                }
            }
        }
    }
}

@media screen and (min-width: @breakpoint__tablet) {
    .iif {
        .etg-row.etg-row__intro {
            .intro-pane {
                .quick-links {
                    ul {
                        &[aria-hidden="true"] {
                            margin-left: auto;
                            margin-right: auto;
                        }

                        li {
                            justify-content: center;
                            margin: 1rem 2rem 1rem 0;
                        }
                    }
                }
            }
        }
    }
}