// Banner (Open Content)
.iif {
    .banner {
        &.banner__home {
            .banner--container {
                .etg-row-container(calc(var(--site-width, 2000px) + 8rem));
            }

            h1 { 
                font-family: var(--font__sans-serif);
                font-weight: 900;
                font-size: 2rem;
                font-size: clamp(2rem, 9vw, 3.75rem);

                &::after {
                    content: "";
                    display: block;
                    width: 100%;
                    max-width: 420px;
                    height: 3px;
                    background: var(--brand-accent, #000);
                }
            }

            .banner--body {
                font-size: 1rem;
                font-size: clamp(1.125rem, 4vw, 1.5rem);

                ul {
                    margin: 0;
                }
            }
        }
    } 
}