.svg-icon {
    width: 5rem;
    height: 5rem;
    margin: 2rem auto;
    display: block;
}

.svg-icon path,
.svg-icon polygon,
.svg-icon rect {
    fill: #212529;
    opacity: 0.75;
}

.svg-icon circle {
    stroke: #35bdf6;
    stroke-width: 1;
}

/// Media Utilities ///
.img-responsive {
    width: 100%;
}

.img__left {
    float: left;
    margin: 0.75em 0.75em 0.75em 0;
}

.img__right {
    float: right;
    margin: 0.75em 0 0.75em 0.75em;
}

.responsive-media {
    max-width: 100%;
    overflow: hidden;
    padding-bottom: percentage((9/16)); // 16:9 aspect ratio
    position: relative;

    iframe,
    object,
    embed {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    &.responsive-media__video-branded {
        overflow: visible;
        margin: 0;

        iframe,
        object,
        embed {
            border: 4px solid var(--brand-accent, #000);
            z-index: 2;
        }

        figcaption {
            z-index: 2;
            position: relative;
            color: #fff;
            margin: 1rem auto;
            padding: 0 2.25rem 0 1rem;
        }

        &::before {
            content: "";
            position: absolute;
            left: -1rem;
            bottom: -1rem;
            width: 100%;
            height: 100%;
            background: var(--brand-blue, #000);
            z-index: 1;
        }
    }

    &.responsive-media__img-branded {
        overflow: visible;
        margin: 0;
        padding: 0;

        img {
            border: 4px solid var(--brand-accent, #000);
            z-index: 2;
            position: relative;
            width: auto;
        }

        figcaption {
            z-index: 2;
            position: relative;
            color: #fff;
            margin: 1rem auto;
            padding: 0 2.25rem 0 1rem;
        }

        &::before {
            content: "";
            position: absolute;
            left: -1rem;
            bottom: -1rem;
            width: 100%;
            height: 100%;
            background: var(--brand-blue, #000);
            z-index: 1;
        }
    }
}
