.iif {
    .event-listing--upcoming {
        padding: 2rem clamp(1rem, 5vw, 3rem);

        .event {
            .event--title {
                font-size: 1.5rem;
                font-size: clamp(1.25rem, 4vw, 1.5rem);
            }
            &:first-child {
                &::before {
                    font-size: 2.5rem;
                    font-size: clamp(1.75rem, 7vw, 2.5rem);
                }
            }
        }
    }
    .event-listing--featured {
        .event {
            .event--title {
                position: relative;
                
                &::before {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    width: 0;
                    height: 2px;
                    background-color: var(--theme-white, #fff);
                    transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
                }
            }
            &:hover {
                .event--title {
                    &::before {
                        left: 0;
                        right: auto;
                        width: 100%;
                    }
                }
            }
        }
    }
}