.vnews-categories {
    h1 {
        display: flex;
        flex-wrap: nowrap;
        font-size: 1.5rem;
        text-align: center;
        margin-bottom: 2rem;
        justify-content: center;
        align-items: center;

        svg {
            width: 40px;
            height: 40px;
            margin-left: 1rem;
            transition: all 0.4s ease;
        }
        
        &[aria-expanded="true"] svg {
            transform: rotate(180deg);
        }
    }

    .vnews-categories--list {
        display: block;
        transition: all var(--timing, 0.4s) var(--timing__function, ease);

        ul {
            display: block;
            margin: 0.5rem 0 1.5rem 1rem;
            padding: 0;
            list-style: none;

            li {
                list-style: none;
                a {
                    color: var(--theme-text, #333);
                    font-size: 1.125rem;
                    font-weight: 800;
                    padding: 0.25rem;
                    transition: all var(--timing__fast, 0.25s) var(--timing__function, ease);

                    span {
                        font-weight: 400;
                    }

                    &:hover {
                        background: #e2e2e2;
                    }
                }
            }

            &:first-child {
                margin-left: 0;
            }
        }
        
        .vnews-categories--label {
            display: block;
            text-transform: uppercase;
            letter-spacing: 1px;
            font-size: 1.25rem;
        }

        &[aria-hidden="true"] {
            max-height: 0;
            margin: 0;
            overflow: hidden;
        }
        
        &[aria-hidden='false'] {
            max-height: 2000px;
            margin: 2rem auto;
        }
    }
}

.vnews-tag {
    display: inline-block;
    position: relative;
    z-index: 2;
    margin-bottom: 1rem;
    font-family: var(--font__sans-serif);
    display: inline-block;
    -webkit-user-select: none;
    user-select: none;
    text-decoration: none;
    vertical-align: middle;
    text-align: center;
    padding: 0.5em 1em;
    font-weight: 600;
    border-radius: 0;
    background: transparent;
    background-image: none;
    transition: all 0.15s ease-in-out;
    box-shadow: none;
    text-shadow: none;
    line-height: var(--line-height__base, 1.5);
    text-transform: uppercase;
    border: 0.2em solid var(--brand-accent, #eee);
    color: var(--brand-accent, #000);
}

.vnews-featured {
    position: relative;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: 800px auto;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background: rgb(241,241,241);
        background: linear-gradient(90deg, rgba(241,241,241,1) 40%, rgba(0,0,0,0) 100%);
        display: none;
    }

    .vnews-featured--article {
        display: inline-block;
        position: relative;
        z-index: 2;
        width: 100%;
        padding: 0 2rem 2rem;
        padding-top: 34%;
        background: linear-gradient(0deg, #f1f1f1 77%, rgba(0, 0, 0, 0) 100%);
    }

    .article--title {
        font-size: 1.5rem;
        color: var(--brand-blue, #00E);
        font-weight: 900;
        font-family: var(--font__sans-serif);
        margin: 0;

        a {
            color: inherit;
        }

        &:hover {
            color: var(--brand-bright-blue, #00E);
        }
    }
    .article--date {
        font-size: 1.25rem;
        margin-bottom: 1.25rem;
        font-weight: 600;
    }

    .article--summary {
        font-size: 1.125rem;
        font-weight: 600;
    }
}

.vnews-recent {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem 2rem;
    background: var(--theme-border, #d2d2d2);

    h1,h2 {
        flex: 1 1 100%;
        font-weight: 600;
        font-size: 1.5rem;
        margin-bottom: 0;
        font-family: var(--font__sans-serif);
    }

    h1 {
        text-transform: uppercase;
    }

    .vnews-recent--article {
        margin: 1rem 0;
        padding: 0 0 1rem 0;
        border-bottom: 1px solid #b5b5b5;
        flex: 1 1 100%;

        .article--topic {
            text-transform: uppercase;
            font-size: 1rem;
            color: var(--brand-bright-blue, #00E);

            a {
                color: inherit;
            }
        }
        .article--title {
            font-size: 1.25rem;
            line-height: 1.4;
            color: var(--brand-blue, #000);
            font-weight: 900;
            text-transform: none;

            a {
                color: inherit;
            }

            &:hover {
                color: var(--brand-bright-blue, #00E);
            }
        }
        .article--date {
            font-size: 1.125rem;
        }
    }

    .vnews-recent--actions {
        flex: 1 1 100%;
        text-align: right;
        font-size: 1.25rem;

        a {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                bottom: 0;
                right: 0;
                width: 0;
                height: 2px;
                background-color: var(--brand-bright-blue, #00E);
                transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
            }

            &:hover {
                &::before {
                    left: 0;
                    right: auto;
                    width: 100%;
                }
            }
        }
    }

    &.vnews-recent__key-topics {
        background: #e2e2e2;

        .vnews-recent--article {
            margin: 1rem 0;
            flex: 1 1 100%;
            border-bottom: 1px solid var(--theme-border, #eee);
        }
        .vnews-recent--actions {
            color: var(--brand-bright-blue, #00E);

            a {
                color: inherit;
            }
        }
    }
}

@media screen and (min-width: @breakpoint__tablet) {
    .vnews-categories {
        h1 {
            pointer-events: none;
            
            svg {
                display: none;
            }
        }
    
        .vnews-categories--list {
            &[aria-hidden="true"] {
                max-height: none;
            }
            
            &[aria-hidden='false'] {
                max-height: none;
            }
        }
    }

    .vnews-featured {
        .vnews-featured--article {
            padding-top: 23%;
        }
    } 
}

@media screen and (min-width: @breakpoint__large) {
    .vnews-featured {
        padding: 1rem 2rem 5rem;
        background-position: top right;
        background-size: cover;
    
        &::before {
            display: block;
        }
    
        .vnews-featured--article {
            max-width: 55%;
            padding: 0;
            background: transparent;
        }
    }

    .vnews-recent {
        &.vnews-recent__key-topics {
            .vnews-recent--article {
                margin: 1rem;
                flex: 1 1 calc(50% - 2rem);

                &:nth-of-type(odd) {
                    margin-left: 0;
                }
                &:nth-of-type(even) {
                    margin-right: 0;
                }
            }
        }
    }
}