// Site Main
.iif {
	.etg-row.etg-row__intro {
		flex-direction: column;
		margin: 0;
		position: relative;
		background: var(--brand-bright-blue, hsla(200, 100%, 36%, 100%));
		color: var(--theme-white, #fff);

		&::before {
			content: "";
			display: block;
			height: 10px;
			background: var(--brand-bright-blue, hsla(200, 100%, 36%, 100%));
			top: -15px;
			position: absolute;
			left: 4rem;
			width: 100%;
		}

		.intro-pane {
			.etg-row-container(var(--site-width, 2000px));
			flex-basis: auto;
			padding-left: var(--grid-gutter-width, 15px);
			padding-right: var(--grid-gutter-width, 15px);

			.DnnModule {
				.module-container {
					margin: 0 0 1rem 0;
				}

				&:first-child,
				&:only-child {
					.module-container {
						margin-top: 3rem !important;
					}
				}

				&:last-child {
					.module-container {
						margin: 0 0 4rem 0;
					}
				}
			}

			a {
				position: relative;

				&::before {
					content: '';
					position: absolute;
					bottom: 0;
					right: 0;
					width: 0;
					height: 2px;
					background-color: var(--brand-blue, #00E);
					transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
				}
	
				&:hover {
					color: var(--theme-link, #00E);
					&::before {
						left: 0;
						right: auto;
						width: 100%;
					}
				}
			}
		}
	}

	.etg-row.etg-row__tiles {
		div[id$='LeftSidebar'] {
			.DnnModule {
				&:first-child {
					.module-container {
						margin-top: 4rem;
					}
				}
			}
		}
		div[id$='HighlightedContent'] {
			padding: 0;

			.DnnModule {
				&:first-child {
					.module-container {
						margin-top: 4rem;
					}
				}
			}
		}
		div[id$='TileMiddleLeft'] {
			padding: 0;
			background: #e2e2e2;
		}
		div[id$='TileMiddleRight'] {
			padding: 0;
			background: var(--theme-border, #d2d2d2);
		}

		.etg-row {
			margin: 0;
		}

		.etg-row--col {

		}

		.module-container {
			margin: 0;
		}
	}

	.etg-row.etg-row__cta {
		margin: 0;

		.DnnModule {
			&:last-child {
				.module-container {
					margin: 0;
				}
			}
		}
	}

	.etg-row.etg-row__promos {
		margin: 0;

		.etg-row--col {
			padding: 0;
		}

		.etg-row__contained {
			div {
				&[id$='PromoLeft'] {
					position: relative;
					background: var(--theme-neutral, #f5f5f5);
					align-items: center;
					display: flex;
					flex-wrap: wrap;
					padding-left: var(--grid-gutter-width, 15px);
					padding-right: var(--grid-gutter-width, 15px);

					.DnnModule {
						flex: 1 1 100%;

						&:first-child {
							margin-top: 3rem;
						}
					}

					&::before {
						content: "";
						display: block;
						background: var(--theme-neutral, #f5f5f5);
						position: absolute;
						right: 100%;
						top: 0;
						height: 100%;
						width: 100%;
					}
				}
				&[id$='PromoRight'] {
					position: relative;
					background: #fff;
					align-items: center;
					display: flex;
					flex-wrap: wrap;
					padding-left: var(--grid-gutter-width, 15px);
					padding-right: var(--grid-gutter-width, 15px);

					.DnnModule {
						flex: 1 1 100%;
						
						&:first-child {
							margin-top: 3rem;
						}
					}

					&::before {
						content: "";
						display: block;
						background: #fff;
						position: absolute;
						left: 100%;
						top: 0;
						height: 100%;
						width: 100%;
					}
				}
			}
		}
	}
}

@media screen and (min-width: @breakpoint__desktop) {
	.iif {
		.etg-row.etg-row__intro {
			.intro-pane {
				padding-left: calc(var(--grid-gutter-width, 15px) * 2);
				padding-right: calc(var(--grid-gutter-width, 15px) * 2);
			}
		}
	}
}