// Banner (Open Content)
.iif {
    .intro-content {
        .etg-row-container(var(--site-width, 2000px));

        .intro-content--item {
            .intro-content--heading {
                font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h1, 2));
                font-size: clamp(2.25rem, 4vw, calc(var(--font-size__base, 18px) * var(--font-factor__h1, 2)));
                color: var(--theme-white, #fff);
            }
            .intro-content--text {
                font-size: 1.5rem;
                font-size: clamp(1.125rem, 2vw, 1.5rem);
    
                p {
                    font-size: inherit;
                }
            }
            .intro-content--media {
                figure.responsive-media {
                    &::before {
                        content: "";
                        position: absolute;
                        left: -1rem;
                        bottom: -1rem;
                        width: 100%;
                        height: 100%;
                        background: var(--brand-blue, #000);
                        z-index: 1;
                    }

                    img, iframe {
                        border: 4px solid var(--brand-accent, #000);
                        z-index: 2;
                    }
                }
            }
        }
    }
}