}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 600;
    src: url('/Portals/_default/Skins/IIF21/fonts/Avenir/AvenirLTCom-Medium.woff') format('woff');
}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 400;
    src: url('/Portals/_default/Skins/IIF21/fonts/Avenir/AvenirLTCom-Book.woff') format('woff');
}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 700;
    src: url('/Portals/_default/Skins/IIF21/fonts/Avenir/AvenirLTCom-Black.woff') format('woff');
}
@font-face {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 900;
    src: url('/Portals/_default/Skins/IIF21/fonts/Avenir/AvenirLTCom-Black.woff') format('woff');
}

body, 
html {	
	font-family: var(--font__sans-serif);
	font-weight: 400;
	font-size: var(--font-size__base, 18px);
	line-height: var(--line-height__base, 1.5);
	color: var(--theme-text, #333);

	.Normal, .NormalDisabled, .NormalDeleted {
		font-family: var(--font__sans-serif);
		font-weight: 400;
		font-size: var(--font-size__base, 18px);
		line-height: var(--line-height__base, 1.5);
		color: inherit;
	}
}

p {
	font-size: var(--font-size__base, 18px);
	line-height: var(--line-height__base, 1.5);
    margin: 0 0 1rem;

	&.lead {
		font-size: calc(var(--font-size__base, 18px) * 1.25);
		line-height: var(--line-height__base, 1.5);
		font-weight: 300;
	}
}

hr {
    color: var(--theme-border, #ededed);
    opacity: 0.25;
}

.highlight {
    padding: 1em;
    background: #f9f9f9;
    border: 1px solid #eee;
}

/// Headings ///
h1,
h2,
h3,
h4,
h5,
h6 {
    color: var(--theme-text, #333);
    font-weight: 400;
	font-family: var(--font__serif);
	line-height: var(--line-height__base, 1.5);
    margin-top: 0;
    margin-bottom: 0.5em;
}
h1 {
	font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h1, 2));
    font-weight: 600;
}
h2 {
	font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h2, 1.5));
    font-family: var(--font__sans-serif);
    font-weight: 900;
    text-transform: uppercase;
}
h3 {
	font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h3, 1.3));
    font-family: var(--font__sans-serif);
    font-weight: 700;
}
h4 {
	font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h4, 1.2));
}
h5 {
	font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h5, 1.2));
	font-weight: bold;
}
h6 {
	font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h6, 1.2));
	font-style: italic;
}

.subtitle {
    font-size: calc(var(--font-size__base, 18px) * var(--font-factor__h4, 1.2));
    color: #666;
}

blockquote {
    margin: 3rem;
    padding: 0.5rem 1rem;

    p {
        color: hsla(var(--theme-brand-base), var(--theme-brand-l), 100%);
        font-size: 1.5em;
        quotes: "“" "”" "‘" "’";
        position: relative;
        font-weight: 700;
        text-shadow: 0 1px 2px var(--theme-border, #eee);
        
        &::before,
        &::after {
            position: absolute;
            font-size: 3.5em;
            color: var(--brand-accent, #00E);
            opacity: 0.9;
            font-family: var(--font__serif);
        }
        
        &::before {
            content: open-quote;
            left: -4.5rem;
            top: -2rem;
        }
        &::after {
            content: close-quote;
            right: -4.5rem;
            bottom: 0;
			top: 0;
        }
    }
    footer {
        color: var(--theme-brand-accent, #00E);
        text-align: center;

        img {
        	width: 4rem;
			height: 4rem;
			border: 3px solid var(--theme-white, #fff);
			border-radius: 50%;
			box-shadow: 0 1px 2px var(--theme-border, #eee);
			margin-right: 1.5rem;
        }
        cite {}
    }
}

