.sr-only {
    // Hide on page but still accessible to screen readers
    position: absolute;
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0 none;
    clip: rect(0, 0, 0, 0);
    overflow: hidden;
}