// Structure

// Resets
* {
    box-sizing: border-box;
}

html {
    overflow-x: hidden;
}

body {
    margin: 0;
    padding: 0;
    background-color: var(--theme-white, #fff);
    overflow-x: hidden;
}

.content-pane {
    flex: 1 1 100%;
}

// Flex Grid System
.etg-row {
    display: flex;
    flex-wrap: wrap;
    margin-left: var(--grid-gutter-width, 15px);
    margin-right: var(--grid-gutter-width, 15px);

    .etg-row--col {
        flex: 1;
        padding-left: var(--grid-gutter-width, 15px);
        padding-right: var(--grid-gutter-width, 15px);
        min-width: 100%;
        flex-basis: 100%;
        
        &.etg-row--col__100 {
            flex: 1 1 100%;
        }
        &.etg-row--col__80 {
            flex: 1 1 80%;
        }
        &.etg-row--col__75 {
            flex: 1 1 75%;
        }
        &.etg-row--col__70 {
            flex: 1 1 70%;
        }
        &.etg-row--col__60 {
            flex: 1 1 60%;
        }
        &.etg-row--col__50 {
            flex: 1 1 50%;
        }
        &.etg-row--col__40 {
            flex: 1 1 40%;
        }
        &.etg-row--col__30 {
            flex: 1 1 30%;
        }
        &.etg-row--col__25 {
            flex: 1 1 25%;
        }
        &.etg-row--col__20 {
            flex: 1 1 20%;
        }

        &.etg-row--col__sidebar {
            flex: 1 1 25%;
            order: 2;
            position: relative;
            z-index: 2;
        }
        &.etg-row--col__main {
            flex: 1 1 75%;
            border-left: 2px solid #eee;
            order: 1;
            position: relative;
            z-index: 1;
        }
    }

    &.etg-row__contained {
        .etg-row-container(var(--site-width, 1400px));
    }

    &.etg-row__404 {
        margin-top: 3em;
    }
}

.etg-row-container(@element-width) {
    margin: 0 auto;
    max-width: @element-width;
}

@media screen and (min-width: @breakpoint__tablet) {
    .etg-row {
        margin-left: calc(var(--grid-gutter-width, 15px) * -2);
        margin-right: calc(var(--grid-gutter-width, 15px) * -2);
    
        .etg-row--col {
            padding-left: calc(var(--grid-gutter-width, 15px) * 2);
            padding-right: calc(var(--grid-gutter-width, 15px) * 2);
        }
    }
}

@media screen and (min-width: @breakpoint__desktop) {
    .etg-row {
        .etg-row--col {
            min-width: 0;
            flex-basis: 0;

            &.etg-row--col__50 {
                min-width: auto;
            }
            &.etg-row--col__25 {
                min-width: auto;
            }

            &.etg-row--col__sidebar {
                padding-left: var(--grid-gutter-width, 15px);
                padding-right: var(--grid-gutter-width, 15px);
                order: 1;
            }
            &.etg-row--col__main {
                flex: 1 1 75%;
                border-left: 2px solid #eee;
                padding-left: calc(var(--grid-gutter-width, 15px) * 3);
                order: 2;
            }
        }
    }
}


