// Site Header
.site--header {
	background: var(--theme-white, #fff) url("/Portals/_default/Skins/IIF21/images/IIF-interior-header-bg-min.png");
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;
	position: relative;
  	color: var(--theme-text, #333);
	padding: 2em 0;
	border-bottom: 2px solid #6A81A5;
	position: relative;

	.header--container {
		width: 100%;
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;
		align-items: center;
		.etg-row-container(calc(var(--site-width, 2000px) + 8rem));
	}

    //Logo
	.site--logo {
		flex: 0 1 320px;
		padding: 0 2rem;
		z-index: 9999;

		img {
            max-width: 100%;
            opacity: 0.98;
		}
	}

	#navToggle {
		background: transparent;
		background-image: none;
		border: 0;
		height: 70px;
		width: auto;
		z-index: 1000;
		color: var(--brand-blue, #00E);
		padding: 0 2em 0 1em;
		outline: none;
		text-transform: uppercase;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		position: relative;
		z-index: 9999;

		.toggle-text {
			font-size: 1.2em;
			font-weight: 900;
			font-family: var(--font__sans-serif);
			margin-right: 1em;
		}

		.icon-bar {
			display: block;
			margin: 5px 0;
			height: 3px;
			background: var(--brand-blue, #00E);
			width: 100%;
			pointer-events: none;
			transition: all var(--timing, 0.4s) var(--timing__function, ease);
			min-width: 30px;
		}

		&::before {
			display: none;
		}

		&[aria-expanded='true'] {
			.icon-bar {
				transform: rotate(47deg) translateY(7px) translateX(6px);

				&:nth-child(2) {
					opacity: 0;
				}
				&:nth-child(3) {
					transform: rotate(-48deg) translateY(-6px) translateX(5px);
				}
			}
		}
	}

	.site--nav {
		flex: 1;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
		padding: 1rem 1rem 2rem;

		//background-color: var(--theme-nav-bg, hsla(198, 80%, 62%, 100%));
		background-color: hsl(200deg, 27%, 87%);
		z-index: 1000;
		position: absolute;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
		top: 100%;
		left: 0;
		right: 0;
		transition: transform var(--timing, 0.4s) ease;

		&[aria-hidden='true'] {
			transform: translate(100%);
		}

		&[aria-hidden='false'] {
			&[data-current-level='1'] {
				transform: translate(-100%);
			}

			&[data-current-level='2'] {
				transform: translate(-200%);
			}

			&[data-current-level='3'] {
				transform: translate(-300%);
			}

			&[data-current-level='4'] {
				transform: translate(-400%);
			}

			&[data-current-level='5'] {
				transform: translate(-500%);
			}
		}
	}

	#dnnSearch {
		flex: 1;
		order: 1;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;

		span {
			&[id$='_ClassicSearch'] {
				position: relative;
				display: block;
				flex: 1;

				.searchInputContainer {
					width: 100%;
					margin: 0;

					input {
						font-size: 1em;
						padding: 1em 5em 1em 1em;
						border: 1px solid #ddd;
						min-width: 0;
						width: 100%;
					}

					a.dnnShow {
						z-index: 999;
					}
				}

				a[id$='_cmdSearch'],
				a.SkinObject {
					position: absolute;
					top: 0;
					right: 0;
					margin: 0;
					width: 3em;
					height: 100%;
					overflow: hidden;

					&::before {
						content: '\f002';
						position: absolute;
						top: 2px;
						bottom: 2px;
						right: 0;
						margin: 0;
						width: 3em;
						display: flex;
						align-content: center;
						background: #fff;
						font-family: var(--font__awesome);
						display: flex;
						flex-wrap: nowrap;
						justify-content: center;
						align-items: center;
						border-right: 1px solid #ddd;
					}

					&:hover,
					&:active,
					&:focus,
					&:focus-within {
						color: var(--brand-accent, #00E);
					}
				}
			}
		}
	}
}

.iif.iif__home {
	.site--header {
		position: relative;
		background: url(/Portals/_default/Skins/IIF21/images/backgrounds/header-default.jpg);
		background-size: cover;
		background-position: center center;
		height: 100vh;
		overflow: hidden;
		border: 0;

		&::before {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background-color: rgba(0,0,0,.35);
			background: linear-gradient(90deg,var(--brand-bright-blue),var(--brand-blue));
			opacity: 0.75;
		}

		.site--utility {
			flex: 0 0 auto;
			display: flex;
			flex-wrap: nowrap;
			align-items: center;
			z-index: 9999;

			.button {
				margin-right: 1rem;
				color: var(--theme-white, #fff);
			}
		}

		.site--nav {
			z-index: 1000;
			position: absolute;
			height: 100%;
			top: 0;
			transition: transform var(--timing, 0.4s) ease;
			background: rgb(5,45,110);
			background: linear-gradient(to bottom right,var(--brand-bright-blue),var(--brand-blue));
			overflow-y: auto;
			padding-right: 1rem;

			&[aria-hidden='true'] {
				transform: translate(100%);
			}
		}

		#navToggle {
			display: block !important;
			color: var(--brand-accent, #00E);
	
			.icon-bar {
				background: var(--brand-accent, #00E);
			}
		}

		.header-pane {
			flex: 1 1 100%;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			display: flex;
			align-items: center;
			justify-content: center;

			.DnnModule {
				width: 100%;
				height: 100%;
				position: relative;

				&.DnnModule-DNN_HTML {
					margin: 0 auto;
					max-width: var(--site-width, 2000px);
					height: auto;
					width: auto;
				}

				* {
					color: var(--theme-white, #fff);
				}

				.module-container,
				.module-container--body {
					width: 100%;
					height: 100%;
					margin: 0;

					> div {
						width: 100%;
						height: 100%;
					}
				}
			}
		}
	}
}
body.dnnEditState {
	.iif.iif__home {
		.site--header {
			.header-pane {
				position: relative;
				height: auto;
				
				.DnnModule {
					min-width: 320px;
				}
			}
		}
	}
}

.iif.iif__landing {
	.site--header {
		border: 0;
	}
}

@media screen and (min-width: @breakpoint__desktop) {
    .site--header  {
		.header--container {
			flex-wrap: wrap;
		}

		#navToggle {
			display: none;
		}

		#dnnSearch {
			span {
				&[id$='_ClassicSearch'] {
					max-width: 50rem;
				}
			}
		}

		.site--nav {
			position: relative;
			top: 0;
			left: 0;
			transform: none;
			background-color: transparent;
			transition: none;
			padding: 0 2rem;

			&[aria-hidden='true'] {
				left: 0;
				transform: none;
			}
		}
    }
}