.iif {
    .content-cards {
        .card {
            margin: 5rem 1rem;
            margin: clamp(3rem, 4vw, 5rem) 1rem;

            .card--heading {
                font-size: 2.25rem;
                font-size: clamp(1.5rem, 4vw, 2.25rem);
                margin-bottom: 3rem;
                margin-bottom: clamp(1.5rem, 4vw, 3rem);
    
            }
            .card--info {
                font-size: 1.5rem;
                font-size: clamp(1rem, 3vw, 1.5rem);
                font-weight: 600;
                margin-bottom: 3rem;
                margin-bottom: clamp(1.5rem, 4vw, 3rem);
    
                p {
                    font-size: inherit;
                }
            }
            .card--actions {
                font-size: 1.875rem;
                font-size: clamp(1.25rem, 3vw, 1.875rem);

                a {
                    position: relative;

                    &::before {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        width: 0;
                        height: 2px;
                        background-color: var(--brand-bright-blue, #00E);
                        transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
                    }
    
                    &:hover {
                        text-decoration: none;

                        &::before {
                            left: 0;
                            right: auto;
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}