// Interior Nav
.interior--nav {
    display: none;
    margin-bottom: 2em;
    
    ul {
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            a,
            span {
                color: var(--theme-nav-link, #00E);
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                width: calc(100% + 1rem + 2px); // This accounts for the 2px border & sidebar padding on hover
                font-size: 1.25em;
                font-weight: 500;
                padding: 1em 1.5em;
                border-right: 2px solid transparent;
                transition: all var(--timing__fast, 0.25s) var(--timing__function, ease);

                .icon {
                    display: none;
                    font-style: normal;
                    position: relative;
                    overflow: hidden;
                    min-width: 2.25rem;

                    &::before {
                        position: relative;
                        margin-right: .5em;
                        font-family: 'Font Awesome 5 Duotone';
                        font-weight: 400;
                        opacity: 1;
                    }
                    &::after {
                        position: absolute;
                        font-family: 'Font Awesome 5 Duotone';
                        left: 0;
                        top: 0;
                        font-weight: 600;
                        opacity: 0.4;
                    }
                }
            }

            &:hover,
            &.selected {
                a {
                    color: var(--theme-nav-link, #00E);
                    background-color: #f2f3f4;
                    border-right: 2px solid var(--theme-brand-accent, #00E);
                }
            }
        }
    }
}

@media screen and (min-width: @breakpoint__desktop) {
    .interior--nav {
        display: block;
    }
}