// Breadcrumb Navigation (DNN)
.breadcrumb {
    background: #f9fafb;
    margin-bottom: 2em;

    .breadcrumb--nav {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        .etg-row-container(var(--site-width, 2000px));
        padding: 2rem;
        color: #999999;
        font-size: 1em;
        border-bottom: 1px solid var(--theme-border, #f2f2f2);
    }

    a {
        &.breadcrumb--home {
            margin-right: 10px;
            background-color: transparent;

            .icon {
                width: 20px;
                height: 22px;
                fill: #ccc;
                vertical-align: top;
            }

            &::after {
                content: '\f054';
                font-family: var(--font__awesome);
                font-weight: 600;
                display: inline-block;
                width: 15px;
                height: 20px;
                margin-left: 15px;
                vertical-align: baseline;
                color: #ccc;
            }
        }
    }

    img {
        display: none;
    }

    span {
        &[itemprop='itemListElement'] {
            margin-right: 15px;
            position: relative;
            a {
                position: relative;

                &[itemprop='item'],
                .SkinObject {
                    color: #b1b1b1;

                    &::after {
                        content: '\f054';
                        font-family: var(--font__awesome);
                        font-weight: 600;
                        display: inline-block;
                        width: 10px;
                        height: 20px;
                        margin-left: 15px;
                        vertical-align: baseline;
                        color: #ccc;
                    }
                }
            }
            span {
                &.SkinObject {
                    &::after {
                        content: ':';
                    }
                }
            }

            &:last-child {
                margin-right: 0;

                a {
                    &[itemprop='item'],
                    .SkinObject {
                        color: var(--theme-link, #00E);
                        &::after {
                            display: none;
                        }
                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    }

    .breadcrumb--home {
        .icon {
            width: 20px;
            height: 18px;
            fill: #ccc;
        }
    }
}

@media screen and (min-width: 500px) {
    .breadcrumb {
        .breadcrumb--nav {
            font-size: 1.25em;
        }

        a {
            &.breadcrumb--home {
                .icon {
                    width: 25px;
                    height: 26px;
                }
            }
        }
    }
}