// Containers
.module-container {
  margin: 0 0 4rem 0;
}

.module-container__callout {
  padding: 2rem;
  background-color: var(--theme-callout, #f3f3f3);
  
  &.module-container__callout-color {
    background-color: var(--brand-bright-blue, #00E);
    color: var(--theme-white, #fff);
  }
}

.module-container__branded {
  .module-container--heading {
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 4px;
      background-color: var(--brand-accent, #00E);
    }
  }
}

.module-container__callout-branded {
  border: 0.333em solid var(--brand-accent);
}