.dnnFormAndList {
	.dnnPrimaryAction {
		font-family: var(--font__sans-serif);
		display: inline-block;
		user-select: none;
		color: var(--theme-white, #fefefe);
		text-decoration: none;
		vertical-align: middle;
		text-align: center;
		font-weight: 400;
		line-height: var(--line-height__base, 1.5);
		text-transform: uppercase;
		border: 0.2em solid hsla(var(--theme-brand-base), 12%, 100%);
		border-radius: 0;
		background-image: none;
		background-color: hsla(var(--theme-brand-base), var(--theme-brand-l), 100%);
		border-color: hsla(var(--theme-brand-base), 12%, 100%);
		transition: all .15s ease-in-out;
		box-shadow: none;
		padding: 0.5em 1em;
	
		&:hover,
		&:active,
		&:focus {
			color: var(--theme-white, #fefefe);
			background: hsla(var(--theme-brand-base), 12%, 100%);
			background-color: hsla(var(--theme-brand-base), 12%, 100%);
			border-color: hsla(var(--theme-brand-base), 12%, 100%);
		}
	
		&:focus-within {
			outline: 3px dotted var(--brand-bright-blue, #00e) !important;
		}
	}
	.dnnSecondaryAction {
		font-family: var(--font__sans-serif);
		display: inline-block;
		user-select: none;
		color: var(--theme-text, #333);
		text-decoration: none;
		vertical-align: middle;
		text-align: center;
		font-weight: 400;
		line-height: var(--line-height__base, 1.5);
		text-transform: uppercase;
		border: 0.2em solid hsla(var(--theme-brand-base), 12%, 100%);
		border-radius: 0;
		background: var(--theme-border, #eee);
		background-color: var(--theme-border, #eee);
		border-color: var(--theme-border, #eee);
		transition: all .15s ease-in-out;
		box-shadow: none;
		text-shadow: none;
		padding: 0.5em 1em;
	
		&:hover,
		&:active,
		&:focus {
			color: var(--theme-text, #333);
			background: hsla(0, 0%, 75%, 100%);
			background-color: hsla(0, 0%, 75%, 100%);
			border-color: hsla(0, 0%, 75%, 100%);
		}
	
		&:focus-within {
			outline: 3px dotted var(--brand-bright-blue, #00e) !important;
		}
	}

	.fnlForm {
		max-width: fit-content;
		margin: 1em 0;

		ul.dnnActions {
			li {
				margin: 0 0.5em;
			}
		}

		.dnnFormItem {
			.dnnFormLabelWithoutHelp {
				margin: 0.5em 0;
			}
	
			input[type="text"], 
			input[type="password"], 
			input[type="email"], 
			input[type="tel"], 
			select, 
			textarea {
				width: 100%;
				max-width: none;
				padding: .75em;
				font-size: 16px;
			}
		}
	}

	.dnnGrid {
		width: 100%;

		.dnnGridItem td, 
		.dnnGridAltItem td {
			padding: 0.5em 0;
		}
	}
}